*{
    margin: 0;
    padding: 0;
  }
  
  html{
    scroll-behavior: smooth;
  }
  
  html, body {
    max-width: 100%;
  }
  
  body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #111;
  }
.navbar{
    position: fixed;
    z-index: 1;
    top: 0;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__black{
    background-color: #111;
}

.navbar__logo{
    position: fixed;
    left: 20px;
    width: 100px;
    padding-left: 20px;
}

.navbar__avatar{
    width: 30px;
}


.right__nav {
    padding-top: 1px;
    display: flex;
    padding-left: 150px;
}

.right__nav nav ul{
    margin: 0;
    padding: 0;
    display: flex;
}

.right__nav nav ul li{
    list-style: none;
}

.right__nav nav ul a{
    text-decoration: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-right: 15px;
    color: white;
}

.active a{
    font-weight: 700;
}

.left__nav {
    display: flex;
    align-items: center;
    padding-right: 60px;
    margin: auto;
}

.left__nav nav ul{
    display: flex;
    align-items: center;
}

.left__nav nav ul li{
    list-style: none;
}

.left__nav nav ul a{
    text-decoration: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-right: 15px;
    color: white;
}

.fas{
    color: #fff;
    font-size: larger;
}

.fa-caret-down{
    font-size: medium;
}

.browse{
    display: none;
}

nav.browse_nav{
    display: flex;
    align-items: center;
    width: 0;
}

nav.browse_nav ul{
    display: flex;
    align-items: center;
} 

nav.browse_nav ul li{
    list-style: none;
}

nav.browse_nav ul a{
    text-decoration: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-right: 15px;
    align-items: center;
    color: white;
}



@media (max-width: 1090px) {
    .navbar__logo {
        left: 0px;
    }

    .hide__left_navlinks {
        display: none;
    }
    .browse_nav {
        padding-left: 115px;
    }
    .browse {
        cursor: pointer;
        display: block;
    }

    .left__nav {
        padding-right: 10px;
    }

    .right__nav nav {
        border-top: solid 1px lightgrey;
        position: absolute;
        width: 100%;
        height: auto;
        background-color: #111;
        top: 60px;
        left: 0;
        transition: 0.5s;
    }
    
    .right__nav .nav_active {
        left: 0;
    }

    .right__nav .nav_notactive {
        left: -100%;
    }
    
    .right__nav nav ul {
        display: block;
    }
    
    .right__nav nav ul li a {
        line-height: 60px;
        margin-bottom: 10px;
        text-align: center;
    }
}
.banner {
    background-size: cover;
    height: 448px;
    color: #fff;
}

.content {
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.banner__title {
    font-size: 2.5rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button {
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover {
    color: rgb(255, 255, 255);
    background-color: #ff0000;
}

.banner__description {
    padding-right:10px ;
    line-height: 1.7;
    margin-top: 1rem;
    font-size: 14px;
    height: 100px;
    overflow-y: scroll;
    max-width: 38rem;
    min-width: 20rem;
}
.banner__rating {
    padding-right:10px ;
    line-height: 1.7;
    margin-top: 1rem;
    font-size: 10px;
    /* height: 100px; */
    /* overflow-y: scroll; */
    max-width: 38rem;
    min-width: 20rem;
}
.banner__language {
    padding-right:10px ;
    line-height: 1.7;
    margin-top: 1rem;
    font-size: 10px;
    /* height: 100px; */
    /* overflow-y: scroll; */
    max-width: 38rem;
    min-width: 20rem;
}

.banner__description::-webkit-scrollbar{
    display: none;
}

.fade_bottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61),#111);
}

@media (max-width: 490px) {
    html{
        overflow-x: hidden; /* Hide horizontal scrollbar */
    }

    .banner{
        margin-right: -20px;
    }   

    .banner__description{
        height: 90px;
    }
}
.row{
    margin-left: 20px;
    color:#fff;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: none;
    color: black;
}
posters::-webkit-scrollbar{
    display: none;
}
.posters::-webkit-scrollbar {
    width: 5px;
}
  
  /* Track */
.posters::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}
   
  /* Handle */
.posters::-webkit-scrollbar-thumb {
    background: red; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  .posters::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }
.poster{
    cursor: pointer;
    max-height: 300px;
    margin-right: 10px;
    
}
.smallPoster{
    cursor: pointer;
    max-height: 200px;
    margin-right: 10px;
    
}
.subTitle{
    padding-top: 5px;
    color: rgb(255, 255, 255);
}
.smallPoster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:  2s;
}
.poster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:  2s;
}
.footer {
    height: 200px;
    color: grey;
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 60%;
    margin-top: 50px;
}

.social-media-icons a {
    margin-right: 25px;
    color: grey;
    font-size: 30px;
}

.social-media-icons a:hover {
    color: grey;
}

.list-items {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 100%;
}

.list-items .column {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
}

.list-items a {
    text-decoration: none;
    color: grey;
    margin-bottom: 15px;
    font-size: 13px;
}

.list-items a:hover {
    color: grey;
    text-decoration: underline;
}

.service-button button {
    border: 1px solid;
    background: none;
    padding: 7px;
    outline: none;
    color: grey;
    margin-bottom: 12px;
    margin-top: 11px;
    cursor: pointer;
}

.copyright p {
    margin-bottom: 20px;
    font-size: 12px;
}
